<template>
  <div class="proj flex-container bg-grey">
    <div class="header shadow">
      <div class="container padding top">
        <div class="logo-wrap">
          <div class="logo">
            <img src="@/assets/logo.png" alt="aten logo" />
          </div>
          <div class="header-title">
            <span class="title">项目下载</span>
          </div>
        </div>
        <div class="menus">
          <ul>
            <li>
              <router-link to="/proj">项目</router-link>
            </li>
            <li>
              <router-link to="/list">资源</router-link>
            </li>
          </ul>
        </div>
        <div class="search" :class="{ 'is-active': focus }">
          <input
            type="text"
            @focus="focus = true"
            @blur="focus = false"
            v-model="keyword"
            placeholder="输入关键词搜索..."
          />
        </div>
        <div class="intro">
          <router-link to="/index/" v-if="userlimit > 100">管理资源</router-link>
          <div v-else>
            <span style="margin-right:20px">{{username}}</span>
            <a href="javascript:" @click="logout">退出登录</a>
          </div>
        </div>
      </div>
    </div>
    <div class="container scroller" ref="scroller">
      <div class="items">
        <div class="item" v-for="(item, index) in list" :key="index">
          <div class="layout">
            <div class="title pro">
              <h3 :title="item.name + ' ' + item.version">{{ item.name }} {{ item.version }}</h3>
              <span class="update el-icon-time">{{ item.updated_at | formatDate }}</span>
            </div>
            <div class="desc">
              <p>{{ item.desc }}</p>
            </div>
            <div class="download">
              <p>
                <span class="el-icon-download">下载次数:</span>
                {{ item.download}}
              </p>
            </div>
            <div class="changelog">
              <h4>更新日志</h4>
              <p>{{ item.log || '暂无' }}</p>
            </div>
            <div class="operation">
              <el-link
                type="danger"
                icon="el-icon-collection-tag"
                @click="$router.push({ name: 'historyproj', params: { id: item.id } })"
                v-if="item.ph_id !== 0"
              >历史版本</el-link>
              <el-link
                type="danger"
                icon="el-icon-collection-tag"
                v-else
                disabled
                title="暂无历史版本"
              >历史版本</el-link>
              <el-link
                type="danger"
                icon="el-icon-download"
                :href="filePath(item)"
                :download="item.file"
                v-if="item.version !== ''"
              >下载</el-link>
              <el-link type="danger" v-else disabled title="暂无历史版本,不可下载" icon="el-icon-download">下载</el-link>
            </div>
          </div>
        </div>
      </div>
      <div class="empty-list" v-if="!list.length">
        <p>暂无符合条件的内容</p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { listProjects, getBaseUrl } from "@/libs/request";
import { getToken, removeCookie } from "@/libs/auth";
import { mapFormat } from "@/libs/tool";
import { formatDate } from "@/libs/moment";
import scrollBehavior from "@/mixins/scrollBehavior";

export default {
  data: () => ({
    softs: [],
    list: [],
    keyword: "",
    focus: false
  }),
  mixins: [scrollBehavior],
  methods: {
    listProjects(callback) {
      listProjects({
        success: res => {
          this.softs = res || [];
          callback();
        },
        fail: err => {
          this.$message.error("获取项目列表失败, " + err);
        }
      });
    },
    filePath({ id, version }) {
      return (
        getBaseUrl() +
        "project/download?id=" +
        id +
        "&version=" +
        version +
        "&token=" +
        getToken()
      );
    },
    search() {
      let { keyword } = this;
      const searchField = (item, keys, keyword) => {
        let vals = keys.map(k => item[k].indexOf(keyword) !== -1);
        return vals.some(d => d);
      };
      this.list =
        keyword !== ""
          ? this.softs.filter(d => {
              let _keyword = keyword.toLowerCase();
              return searchField(d, ["name", "desc"], _keyword);
            })
          : this.softs;
        
        this.$nextTick(() => this.setScroll(this.$refs.scroller))
    },
    mapFormat,
    logout() {
      removeCookie();
      this.$router.push("/");
    }
  },
  computed: {
    ...mapState(["userlimit", "username"])
  },
  filters: { formatDate },
  mounted() {
    this.listProjects(this.search);
  },
  watch: {
    keyword: "search"
  }
};
</script>

<style scoped>
.menus ul {
  display: flex;
}

.menus ul li a {
  display: block;
  font-size: 18px;
  color: #666;
  padding: 0 10px;
}

.menus ul li .router-link-exact-active {
  color: #da251d;
}
</style>
